<template>
  <div class="homePage">
    <!-- 全屏背景图 -->
    <div class="img-box">
      <hoverShowVideo ref="hoverShowVideo" videoOrGif="gif" />
    </div>
    <!-- 全屏背景图 -->

    <!-- 欢迎语 -->
    <div class="welcome-box positionCenter">
      <welcomeBox ref="welcomeBox" />
    </div>
    <!-- 欢迎语 -->

    <!-- 导航栏 -->
    <div :class="isCollapse ? 'menu-box-collapse' : 'menu-box'">
      <menuBox ref="menuBox" :isCollapse="isCollapse" />
    </div>
    <!-- 导航栏 -->

    <!-- 开源项目 -->
    <div class="open-box">open-box</div>
    <!-- 开源项目 -->

    <!-- 输入密码的弹窗 -->
    <passwordDialog ref="passwordDialog" />
    <!-- 输入密码的弹窗 -->
  </div>
</template>

<script>
import { makePassword, checkPassword } from "@/utils/password";
import passwordDialog from "@/views/homePage/passwordDialog";
import menuBox from "@/views/homePage/menuBox";
import { mapActions } from "vuex";

export default {
  components: { passwordDialog, menuBox },
  data() {
    return {
      isCollapse: false,
    };
  },
  methods: {
    ...mapActions(["ADD_PASSWORD"]),

    handleScroll(e) {
      if (
        !this.$store.state.auth.isScroll &&
        !JSON.parse(localStorage.getItem("isScroll"))
      )
        this.$refs.passwordDialog.showDialog();
    },

    setPassword() {
      this.ADD_PASSWORD(makePassword());
    },

    changeIsCollapse() {
      this.isCollapse = !this.isCollapse;
    },
  },
  created() {
    this.setPassword();
  },
  mounted() {
    window.addEventListener("scroll", (e) => {
      e.preventDefault();
      this.handleScroll(e);
    });
  },
};
</script>

